import { Col, Row } from 'antd'
import React from 'react'
import { Link } from 'react-router-dom'

interface IHomeCenterBannerCard {
  id: string,
  title: string,
  desc: string,
  img: string,
  price: number,
  old_price: number,
  toUrl: string
}

function HomeCenterBannerCard(props: IHomeCenterBannerCard) {

  const {
    title,
    desc,
    img,
    price,
    old_price,
    toUrl
  } = props;

  return (
    <div className="center_banner_card">
      <Row gutter={[16, 16]}>
        <Col lg={9} md={9} sm={24} xs={24}>
          <div className="left">
            <div className="left_card">
              <h1 className="banner_title">
                {title}
              </h1>
              <p className="banner_desc">
                {desc}
              </p>
            </div>
          </div>
        </Col>
        <Col lg={6} md={6} sm={24} xs={24}>
          <div className="center">
            <img src={img} alt={title} />
          </div>
        </Col>
        <Col lg={9} md={9} sm={24} xs={24}>
          <div className="right">
            <div>
              <h1 className="price title42_extra_bold">
                {price} Сум
              </h1>
              <del className="old_price">
                {old_price} Сум
              </del>
            </div>
            <Link className="banner_link" to={toUrl}>
              Показать еще
            </Link>
          </div>
        </Col>
      </Row>
    </div>
  )
}

export default HomeCenterBannerCard